<template>
  <div>
    <div class="container-fluid productManagement">
      <!-- START: Card Data-->

      <div class="row">
        <div class="col-12 col-md-12">
          <div class="card">
            <div class="card-header p-0">
              <div class="col-12 col-sm-11 ml-5 mr-4 mt-5">
                <div class="form-row ml-3">
                  <div class="form-group row col-md-6">
                    <label for="username" class="col-sm-3 col-form-label pr-0 pl-0">Comments</label>
                    <div class="col-sm-8">
                      <textarea v-model="$v.comments.$model" class="form-control"></textarea>
                      <div v-if="commentError">
                        <span
                          class="text-danger"
                          v-if="!$v.comments.required"
                        >Please provide the comments</span>
                      </div>
                    </div>
                  </div>
                </div>
                <button
                  class="btn btn-primary float-right mb-4 pl-5 pr-5 p-2"
                  name="Approve"
                  value="APPROVE"
                  @click="Approve"
                >APPROVE</button>
                <router-link class="btn btn-outline-primary mb-4 mr-3 p-2 pl-5 pr-5" style="float:right;" type="button" to="/StockRequestList">BACK</router-link>
              </div>
              <div class="clearfix"></div>
            </div>
          </div>
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { required } from "vuelidate/lib/validators";
import toast from '@/toast';

export default {
  data() {
    return {
      commentError: false,
      comments:''
    };
  },
  validations: {
    comments: { required },
  },
  props: ["requestId"],
  created() {
    
    //this.role = decodedToken.Role;
  },
  methods: {
    Approve: function () {
      this.$v.$touch();
      this.commentError = this.$v.comments.$anyError;
      if (!this.commentError) {
        axios.post(process.env.VUE_APP_OrderMgmtApisBaseUrl +"/InternalStock/ApproveStockRequest",{ id: this.requestId, comments: this.comments })
          .then(() => {
            toast.success("Aproved!..");
            this.$router.push({ name: "StockRequestList" });
          });
      }
    },
  },
};
</script>
