<template>
  <div>
    <div class="d-block header-section">
      <div class="py-3 align-self-center d-sm-flex w-100 rounded">
        <div class="w-sm-100 mr-auto">
          <h1>
            <b>Stock Request</b>
          </h1>
        </div>
      </div>
    </div>
    <div class="container-fluid productManagement">
      <!-- START: Card Data-->
      <div class="card mb-4">
        <div class="row p-5">
          <div class="col-12 col-md-4 p-3 row mob-c">
                <div class="col-md-3">
                  <img
                    src="@/assets/images/icons/order-one.png"
                    class="mob-img"
                  />
                </div>
                <div class="col-md-9 mt-2">
                  <h5 class="text-muted">Stock Request Number</h5>
                  <h5 class="d-flex align-items-left text-primary">
                    {{ stockRequest.number }}
                  </h5>
                </div>
              </div>
          <div class="col-12 col-md-4 border-left p-3 row">
            <div class="col-md-3">
              <img src="@/assets/images/icons/order-three.png" />
            </div>
            <div class="col-md-9 mt-2">
              <h5 class="text-muted">Requested Facility</h5>
              <h5 class="d-flex align-items-left text-primary">{{stockRequest.requestingFacilityName}}</h5>
            </div>
          </div>
          <div class="col-12 col-md-4 border-left p-3 row">
            <div class="col-md-3">
              <img src="@/assets/images/icons/order-two.png" />
            </div>
            <div class="col-md-9 mt-2">
              <h5 class="text-muted">Status</h5>
              <h5 class="d-flex align-items-left text-primary">{{stockRequest.status}}</h5>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-12 col-md-12">
          <div class="card">
            <div class="card-header p-0">
              <div class="col-12 col-sm-11 ml-5 mr-4 mt-5">
                <div class="table-responsive">
                      <table class="table layout-secondary bordered">
                        <thead>
                          <tr>
                            <th>Product</th>
                            <th>No of Stamps Required</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr style="background: #fff;">
                            <td>{{stockRequest.productName}}</td>
                            <td>{{stockRequest.noOfStamps}}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>                
                <div class="row">
                  <div class="col-12 col-sm-12 mt-3 mb-5">
                    <p class="text-muted mb-4">History</p>
                    <div class="table-responsive">
                      <table class="table layout-secondary bordered">
                        <thead>
                          <tr>
                            <th>Date</th>
                            <th>User</th>
                            <th>Action</th>
                            <th>Note</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="history in stockRequest.internalStockRequestHistories" :key="history.id" style="background: #fff;">
                            <td>{{history.modifiedDate}}</td>
                            <td>{{history.userName}}</td>
                            <td>{{history.action}}</td>
                            <td></td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <div class="clearfix"></div>
            </div>
          </div>
        </div>
      </div>
      <!-- END: Card DATA-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import roles from '../../router/roles';
export default {
  data() {
    return {
      userLocationId: null,
      role: "",
      roles: [],
      stockRequest:{},
    };
  },
  props: ["requestId"],
  components: {},
  methods: {
    getStockRequest() {},
  },
  mounted() {
    console.log("RequestId:" + this.requestId);
    axios
      .get(
        process.env.VUE_APP_OrderMgmtApisBaseUrl +
          "/InternalStock?requestId=" +
          this.requestId
      )
      .then((response) => {
        this.stockRequest = response.data;
      });
  },

  created() {
    this.role = this.$store.state.clientAppDecodedToken.Role;
    this.userLocationId = this.$store.state.clientAppDecodedToken.LocationId;
    this.roles = roles;
    console.log("Created:" + this.requestId);
  },
};
</script>
